<template>
  <div>
    <div class="row">
      <div class="col-md-11 col-sm-6">
        <h3 class="fw-bold">Edit Business</h3>
      </div>
      <div class="col-md-1 col-sm-6 my-2">
        <router-link to="/admin/businesslist">
          <v-btn color="error">
            <v-icon left class="mt-1">mdi-format-list-bulleted</v-icon>List</v-btn>
        </router-link>
      </div>
    </div>
    <div class="my-5">
      <v-form @submit.prevent="businessAddFormSubmit" ref="form" v-model="valid" lazy-validation>
        <v-container>
          <v-row>
            <v-col cols="12" sm="6" md="12">
              <v-text-field 
                  v-model="businessesList.name"
                  label="Name" 
                  prepend-icon="mdi-account"></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="12">
              <v-text-field 
                  v-model="businessesList.email"
                  label="E-mail"
                  prepend-icon="mdi-email"></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="12">
                <v-autocomplete
                  v-model="businessesList.plan_id"
                  :items="plans"
                  label="Change plan"
                  item-text="plan_description"
                  item-value="id"
                  prepend-icon="mdi-domain"></v-autocomplete>
            </v-col>
            <v-col cols="12" sm="6" md="12">
              <v-text-field prepend-icon="mdi-phone" 
                  v-model="expiredDate"
                  label="Trial expiry date" readonly></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="12">
              <v-text-field 
                  v-model="reseller_id" 
                  :error-messages="errors" 
                  label="Change Reseller"
                  prepend-icon="mdi-account" readonly></v-text-field>
            </v-col>
          </v-row>
          <v-col cols="12" md="12" class="py-0 d-flex" style="justify-content: space-between;">
                <div>
                  <v-checkbox v-model="businessesList.user_status" label="Active" value="1"></v-checkbox>
                </div>
                <div class="ms-3 pb-3">
                    <v-btn v-if="isLoading" class="my-2 me-3 cred">Loading...</v-btn>
                    <v-btn v-else class="my-2 me-3 cred" type="submit" :disabled="!valid">UPDATE</v-btn>
                </div>
          </v-col>

        </v-container>
      </v-form>
    </div>

    <v-snackbar v-model="snackbar" :timeout="snackbar.timeout" class="snackbar-shadow">{{ text }}
      <template v-slot:action="{ attrs }">
        <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">Close</v-btn>
      </template>
    </v-snackbar>

  </div>
</template>

<script>
import axios from "axios";
export default {
    name: "ResellersUpdate",
    props:{
      id:Number
    },
    components: {  },
    data() {
      return {
          isLoading: false,
          valid: false,
          businessesList: {},
          expiredDate: '',
          reseller_id: '',
          plans: '',
          snackbar: false,
          text: '',
          timeout: '',
      }
    },
    mounted() {
      this.getBusinessUser();
      this.All_plans();
    },
    methods:{
        getBusinessUser() {
          axios.get('admin/businesses/single/get/'+this.id)
            .then((resp)=>{
              this.businessesList = resp.data.businesses;
              if(this.businessesList.added_date!='0000-00-00'){
                this.expiredDate = this.addedExpiredDate(this.businessesList.added_date, this.businessesList.plan_expiry);
              }else{
                this.expiredDate = "---";
              }
              if(this.businessesList.listedby_reseller_id!=null){ // Reseller id
                this.reseller_id = "RES"+this.businessesList.listedby_reseller_id;
              }else{
                this.reseller_id = "---";
              }
            });
        },
        addedExpiredDate(added_date, plan_expiry){ // expired date
          var date = new Date(added_date);
          date.setDate(date.getDate() + plan_expiry);
          const currentDate = date.getFullYear()+"-"+date.getMonth()+"-"+date.getDate();
          return currentDate;
        },
        All_plans(){
          axios.get('plans')
            .then((resp)=>{
              this.plans = resp.data.plans;
            });
        },
        businessAddFormSubmit() {
          this.isLoading = true;
          const config = {
              headers: { 'content-type': 'multipart/form-data' }
          }
          let data = { 
            user_id: this.businessesList.id,
            business_id: this.businessesList.businesses_id,
            name: this.businessesList.name,
            email: this.businessesList.email,
            plan_id: this.businessesList.plan_id,
            user_status: this.businessesList.user_status,
          };
          if(this.businessesList.id!='' && this.businessesList.businesses_id && this.businessesList.name!='' && this.businessesList.email!='' && this.businessesList.plan_id!=''){
            axios.post('admin/businesses/update', data, config)
              .then((resp) => {
                  this.snackbar = true;
                  this.text = resp.data.message;
                  this.timeout = 3000;
                  this.isLoading = false;
            });
          }else{
            this.snackbar = true;
            this.text = "All inputs are required.";
            this.timeout = 3000;
            this.isLoading = false;
          }
          
        }
    }
}
</script>